import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { IViewComponentDefinition, IViewDefinition, RX_LAUNCH_BEHAVIOR } from '@helix/platform/view/api';
import { get } from 'lodash';
import { ILaunchUrlViewActionParams } from './launch-url-view-action-params.interface';

@Injectable({
  providedIn: 'root'
})
export class RxLaunchUrlViewActionDefinitionAdapterService
  implements IDefinitionAdapter<IViewComponentDefinition, IViewDefinition>
{
  adaptDefinition(viewComponentDefinition) {
    const propertiesByName: ILaunchUrlViewActionParams = get(viewComponentDefinition, 'propertiesByName', {});

    propertiesByName.launchBehavior = propertiesByName.launchBehavior ?? RX_LAUNCH_BEHAVIOR.newWindow.value;
  }
}
